<template>
  <div class="setting-box">
    <el-card class="box-card">
      <el-form
        :model="goalForm"
        status-icon
        ref="goalForm"
        label-position="left"
        label-width="80px"
      >
        <el-form-item label="目标金额" prop="fee">
          <el-input v-model="goalForm.fee" @change="((val)=>{changeValue(val, 'fee')})"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('goalForm')">GO</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import { mixObj } from "@/mixin/mixin";

export default {
  name: "",
  mixins: [mixObj],
  components: {},
  created() {
    this.checkLogin(this.getCheckLogin);
  },
  data() {
    return {
      goalForm: {
        fee: ""
      }
    };
  },
  computed: {
    ...mapGetters("moduleUser", {
      getCheckLogin: "getCheckLogin"
    })
  },
  methods: {
    ...mapMutations("moduleUser", {
      setGoal: "setGoal"
    }),
    submitForm(formName) {
      if (this[formName].fee !== "") {
        this.setGoal(this[formName].fee);
      }
      this.$router.go(-1);
    },
    changeValue(value, type) {
      this.goalForm[type] = this.formatFee(value);
    }
  }
};
</script>

<style lang="scss" scoped>
.box-card {
  width: 20em;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media (max-width: 768px) {
  .box-card {
    width: 20em;
  }
}

@media (max-width: 568px) {
  .box-card {
    width: 16em;
  }
}
</style>
