<template>
  <div class="tally-box">
    <el-card class="box-card">
      <el-form :model="feeForm" status-icon ref="feeForm" label-position="left" label-width="60px">
        <el-form-item label="吃喝" prop="food">
          <el-input v-model="feeForm.food" @change="((val)=>{changeValue(val, 'food')})"></el-input>
        </el-form-item>
        <el-form-item label="玩乐" prop="entertainment">
          <el-input
            v-model="feeForm.entertainment"
            @change="((val)=>{changeValue(val, 'entertainment')})"
          ></el-input>
        </el-form-item>
        <el-form-item label="购物" prop="shopping">
          <el-input v-model="feeForm.shopping" @change="((val)=>{changeValue(val, 'shopping')})"></el-input>
        </el-form-item>
        <el-form-item label="话费" prop="telephone">
          <el-input v-model="feeForm.telephone" @change="((val)=>{changeValue(val, 'telephone')})"></el-input>
        </el-form-item>
        <el-form-item label="车费" prop="transport">
          <el-input v-model="feeForm.transport" @change="((val)=>{changeValue(val, 'transport')})"></el-input>
        </el-form-item>
        <el-form-item label="其他" prop="other">
          <el-input v-model="feeForm.other" @change="((val)=>{changeValue(val, 'other')})"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('feeForm')">GO</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import { mixObj } from "@/mixin/mixin";

export default {
  name: "",
  mixins: [mixObj],
  components: {},
  created() {
    this.checkLogin(this.getCheckLogin);
    this.feeForm = this.getTodayData;
  },
  data() {
    return {
      feeForm: {
        food: "",
        entertainment: "",
        shopping: "",
        telephone: "",
        transport: "",
        other: ""
      }
    };
  },
  computed: {
    ...mapGetters("moduleUser", {
      getCheckLogin: "getCheckLogin"
    }),
    ...mapGetters("moduleTableData", {
      getTodayData: "getTodayData"
    })
  },
  methods: {
    ...mapMutations("moduleTableData", {
      setTodayData: "setTodayData"
    }),
    submitForm(formName) {
      this.setTodayData(this[formName]);
      this.$router.push("visual");
    },
    changeValue(value, type) {
      this.feeForm[type] = this.formatFee(value);
    }
  }
};
</script>

<style lang="scss" scoped>
.box-card {
  width: 20em;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media (max-width: 768px) {
  .box-card {
    width: 20em;
  }
}

@media (max-width: 568px) {
  .box-card {
    width: 16em;
  }
}
</style>
