const User = {
    namespaced: true,
    state: {
        goal: 0,
        name: '',
        checkLogin: false
    },
    mutations: {
        setName(state, value) {
            state.name = value;
        },
        setGoal(state, value) {
            state.goal = value;
        },
        setCheckLogin(state, value) {
            state.checkLogin = value;
        }
    },
    actions: {},
    getters: {
        getName: state => state.name,
        getGoal: state => state.goal,
        getCheckLogin: state => state.checkLogin
    }
}

export default User