<template>
  <div class="login-box">
    <el-card class="box-card">
      <el-form :model="loginForm" status-icon :rules="rules" ref="loginForm">
        <el-form-item label="用户名" prop="name">
          <el-input type="username" v-model="loginForm.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="pass">
          <el-input type="password" v-model="loginForm.pass" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('loginForm')">GO</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

export default {
  name: "",
  components: {},
  created(){
    this.setCheckLogin(false);
  },
  data() {
    let validateName = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入用户名"));
      } else {
        callback();
      }
    };
    let validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        callback();
      }
    };
    return {
      loginForm: {
        name: "",
        pass: ""
      },
      rules: {
        name: [{ required: true, validator: validateName, trigger: "blur" }],
        pass: [{ required: true, validator: validatePass, trigger: "blur" }]
      }
    };
  },
  methods: {
    ...mapMutations("moduleUser", {
      setName: "setName",
      setCheckLogin: "setCheckLogin"
    }),
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          // 需要后台验证用户名和密码是否正确
          if (this.checkLogin(this.loginForm)) {
            this.setName(this.loginForm.name);
            this.setCheckLogin(true);
            this.$router.push("tally");
          } else {
            alert("账号或密码错误！！！");
            this.$refs[formName].resetFields();
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    checkLogin({ name = "", pass = "" } = {}) {
      return name === "Landscape" && pass === "123";
    }
  }
};
</script>

<style lang="scss" scoped>
.box-card {
  width: 20em;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media (max-width: 768px) {
  .box-card {
    width: 20em;
  }
}

@media (max-width: 568px) {
  .box-card {
    width: 16em;
  }
}
</style>
