<template>
  <div class="visual-box">
    <el-table :data="tableData" border style="width: 90%; margin:0 auto">
      <el-table-column
        v-for="col in tableColumn"
        :key="col.prop"
        :prop="col.prop"
        :label="col.label"
      ></el-table-column>
    </el-table>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import { mixObj } from "@/mixin/mixin";

export default {
  name: "",
  mixins: [mixObj],
  components: {},
  created() {
    this.checkLogin(this.getCheckLogin);
    this.getDataFromApi();
  },
  data() {
    return {
      tableColumn: [
        {
          prop: "date",
          label: "日期"
        },
        {
          prop: "food",
          label: "吃喝"
        },
        {
          prop: "entertainment",
          label: "玩乐"
        },
        {
          prop: "shopping",
          label: "购物"
        },
        {
          prop: "telephone",
          label: "话费"
        },
        {
          prop: "transport",
          label: "车费"
        },
        {
          prop: "other",
          label: "其他"
        }
      ],
      tableData: []
    };
  },
  computed: {
    ...mapGetters("moduleUser", {
      getCheckLogin: "getCheckLogin"
    }),
    ...mapGetters("moduleTableData", {
      todayData: "getTodayData"
    })
  },
  methods:{
    getDataFromApi(){
      this.$api.get(
        "/data/tableData",
        null,
        Response => {
          this.tableData = Response;
          this.tableData.unshift(this.todayData);
        },
        Error => {
          throw new Error(Error);
        }
      );
    }
  }
};
</script>

