import Mock from 'mockjs' // 引入mockjs

const Random = Mock.Random // Mock.Random 是一个工具类，用于生成各种随机数据

let data = [] // 用于接受生成数据的数组
let size = [
        '300x250', '250x250', '240x400', '336x280',
        '180x150', '720x300', '468x60', '234x60',
        '88x31', '120x90', '120x60', '120x240',
        '125x125', '728x90', '160x600', '120x600',
        '300x600'
    ] // 定义随机值
for (let i = 0; i < 10; i++) { // 可自定义生成的个数
    let template = {
        'food': Random.float(0, 100, 0, 2), // 可以生成基本数据类型
        'entertainment': Random.float(0, 100, 0, 2), // 生成1到100之间自然数
        'shopping': Random.float(0, 100, 0, 2), // 生成1到100之间的整数
        'telephone': Random.float(0, 100, 0, 2), // 生成0到100之间的浮点数,小数点后尾数为0到5位
        'transport': Random.float(0, 100, 0, 2), // 生成0到100之间的浮点数,小数点后尾数为0到5位
        'other': Random.float(0, 100, 0, 2), // 生成0到100之间的浮点数,小数点后尾数为0到5位
        'date': Random.date(), // 生成一个随机日期,可加参数定义日期格式
    }
    data.push(template)
}

export default data;