      class="el-menu-demo"
      mode="horizontal"
      @select="handleSelect"
      :router="routerOpen"
    >
      <template>
        <el-menu-item index="tally">
          <div class="el-menu-title">Tally Book</div>
        </el-menu-item>
        <el-menu-item index="setting" v-if="getCheckLogin">
          <div class="el-menu-title">Hi {{ username }}, your goal fee is {{ thousandFormat(goal) }} RMB</div>
        </el-menu-item>
      </template>
    </el-menu>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import { mixObj } from "@/mixin/mixin";
export default {
  name: "",
  mixins: [mixObj],
  components: {},
  data() {
    return {
      routerOpen: true,
      pathname: document.location.pathname
    };
  },
  computed: {
    ...mapGetters("moduleUser", {
      username: "getName",
      getCheckLogin: "getCheckLogin",
